  min-height: var(--vh, 100vh);
}

.main-content {
  display: flex;
  flex: 1;
}
.main-content > gx-card {
  display: flex;
  flex: 1;
}

body {
  scrollbar-width: thin;
  scrollbar-color: var(--gx-scrollbar-thumb-color) var(--gx-scrollbar-background-color);
}

::-webkit-scrollbar {
  width: 11px;
}

::-webkit-scrollbar-track {
  background: var(--gx-scrollbar-background-color);
}

::-webkit-scrollbar-thumb {
  background-color: var(--gx-scrollbar-thumb-color);
  border-radius: 6px;
  border: 3px solid var(--gx-scrollbar-background-color);
}

virtual-scroller {
  flex: 1;
}
virtual-scroller .scrollable-content {
  height: auto !important;
}

