  margin-bottom: 0px;
}

gx-canvas-cell {
  pointer-events: none;
}

.flex-cell {
  display: flex;
  overflow: hidden;
}
.flex-cell > gx-textblock {
  width: 100%;
  height: 100%;
}
.flex-cell > gx-button {
  flex: 1;
}

